import React, { Fragment } from 'react'
import { connect } from 'react-redux'
import { useParams } from 'react-router-dom'

const ForgotPasswordConfirm = () => {
  const { email } = useParams()
  return (
    <>
      <section className='container'>
        <h1 className='large text-primary'>Recuperar Contrasea</h1>
        <p className='lead'>Se ha enviado un mail a <i>{email}</i> con el link para continuar con el proceso de recuperacin de la contrasea</p>
      </section>
    </>
  )
}

export default connect()(ForgotPasswordConfirm)
